





















import Vue from "vue";
import TreeView from "@/components/TreeView.vue";

export default Vue.extend({
	components: {
		TreeView
	},
	mounted() {
		var navigation: any = this.$refs.navigation;
		addEventListener("scroll", e => {
			if (scrollY > 55) navigation.style.position = "fixed";
			else if (innerWidth > 480) navigation.style.position = "static";
		});
		var startX: number;
        var endX: number;
        var currentLeft = -innerWidth;
		addEventListener("touchstart", e => {
			startX = e.touches[0].clientX;
		});
		addEventListener("touchmove", e => {
            endX = e.touches[0].clientX;
            var value = currentLeft-startX+endX
            navigation.style.left = (value < -innerWidth ? -innerWidth : (value > 0 ? 0 : value))+"px"
		});
		addEventListener("touchend", e => {
			if (startX - endX > 50) {
                currentLeft = -innerWidth;
				navigation.style.left = "-100%";
			} else if (startX - endX < 50) {
                currentLeft = 0;
				navigation.style.left = "0px";
			}
		});
	},
	data() {
		return {
			search: "",
			isShowMenu: false,
			list: [
				{
					name: "구성 요소",
					children: [
						{
							name: "Camera",
							children: [
								{
									name: "CameraBuilder",
									children: []
								}
							]
						},
						{
							name: "Object",
							children: [
								{
									name: "ObjectBuilder",
									children: []
								}
							]
						},
						{
							name: "Scene",
							children: []
						},
						{
							name: "Component",
							children: []
						}
					]
				},
				{
					name: "시스템",
					children: [
						{
							name: "GraphicManager",
							children: []
						},
						{
							name: "InputManager",
							children: []
						},
						{
							name: "SceneManager",
							children: []
						},
						{
							name: "TextureManager",
							children: []
						},
						{
							name: "TimeManager",
							children: []
						},
						{
							name: "WindowManager",
							children: []
						}
					]
				},
				{
					name: "컴포넌트",
					children: [
						{
							name: "Effect",
							children: [
								{
									name: "EffectInfo",
									children: []
								}
							]
						},
						{
							name: "Renderer",
							children: [
								{
									name: "SpriteRenderer",
									children: []
								},
								{
									name: "ViewRenderer",
									children: []
								},
								{
									name: "TextRenderer",
									children: []
								},
								{
									name: "AnimationRenderer",
									children: []
								}
							]
						},
						{
							name: "Transform",
							children: []
						}
					]
				}
			]
		};
	},
	computed: {
		getComputedList(): any {
			if (this.search.length != 0) {
				var result: any[] = JSON.parse(JSON.stringify(this.list));
				var findName = (item: any) => {
					if (item.hasOwnProperty("children")) {
						if (item.name.indexOf(this.search) != -1) {
							return true;
						}
						var chk = false;
						item.children = item.children.filter((y: any) => {
							let value = findName(y);
							if (!chk) chk = value;
							return value;
						});
						return chk;
					} else {
						if (item.name.indexOf(this.search) != -1) {
							return true;
						} else {
							return false;
						}
					}
				};
				return result.filter((x: any) => {
					return findName(x);
				});
			} else return this.list;
		}
	}
});
